<template>
  <v-card-text>
    <v-container>
      <v-row>
        <v-col>
          <v-text-field
            v-model="menu.title"
            :label="$t('forms.labels.title')"
            outlined
            :rules="[rules.required]"
          ></v-text-field>
          <div class="mb-5">
            <v-btn color="primary" @click="addDialog">
              <v-icon>mdi-plus</v-icon>
              Add New Link
            </v-btn>
          </div>
          <div v-if="menu.items.length === 0">
            <h1>Please Add Menu Items</h1>
          </div>
          <div v-else>
            <draggable
              v-model="menu.items"
              group="people"
              @start="drag = true"
              @end="drag = false"
            >
              <div
                v-for="(element, index) in menu.items"
                :key="element.index"
                class="mb-2"
              >
                <div
                  class="elevation-2 d-flex primary white--text py-2 px-3 rounded cursor-move"
                >
                  <div>{{ element.title }}</div>
                  <v-spacer></v-spacer>
                  <v-icon
                    color="success"
                    @click="editDialog(element, index, null, 'item')"
                    >mdi-pencil</v-icon
                  >
                  <v-icon
                    color="error"
                    @click="deleteDialog(element, index, null, 'item')"
                    >mdi-delete</v-icon
                  >
                </div>
                <div class="ml-10">
                  <draggable
                    v-model="element.children"
                    group="people"
                    class="pb-5"
                    @start="drag = true"
                    @end="drag = false"
                  >
                    <div
                      v-for="(el, i) in element.children"
                      :key="el.i"
                      class="mt-2"
                    >
                      <div
                        class="elevation-2 d-flex primary white--text py-2 px-3 rounded cursor-move"
                      >
                        <div>{{ el.title }}</div>
                        <v-spacer></v-spacer>
                        <v-icon
                          color="success"
                          @click="editDialog(el, index, i, 'children')"
                          >mdi-pencil</v-icon
                        >
                        <v-icon
                          color="error"
                          @click="deleteDialog(el, index, i, 'children')"
                          >mdi-delete</v-icon
                        >
                      </div>
                    </div>
                  </draggable>
                </div>
              </div>
            </draggable>
          </div>
        </v-col>
      </v-row>
      <v-row justify="center">
        <v-dialog v-model="dialog" persistent max-width="600px">
          <v-card>
            <v-card-title>
              <span class="headline">{{
                remove ? 'Delete Link' : edit ? 'Edit Link' : 'Add Link'
              }}</span>
            </v-card-title>
            <v-card-text>
              <v-container>
                <v-row>
                  <v-col>
                    <v-form ref="menuItemForm" v-model="valid">
                      <v-text-field
                        v-model="menuItem.title"
                        :label="$t('forms.labels.title')"
                        outlined
                        :rules="[rules.required]"
                      ></v-text-field>
                      <v-text-field
                        v-model="menuItem.translation"
                        :label="$t('forms.labels.translation')"
                        outlined
                      ></v-text-field>
                      <v-text-field
                        v-model="menuItem.icon"
                        :label="$t('forms.labels.icon')"
                        outlined
                      ></v-text-field>
                      <v-text-field
                        v-model="menuItem.route"
                        :label="$t('forms.labels.route')"
                        outlined
                      ></v-text-field>
                      <v-switch
                        v-model="menuItem.loggedIn"
                        :label="$t('forms.labels.showWhenLoggedIn')"
                      ></v-switch>
                      <v-switch
                        v-model="menuItem.loggedOut"
                        :label="$t('forms.labels.showWhenLoggedOut')"
                      ></v-switch>
                      <v-select
                        v-model="menuItem.permission"
                        :items="[
                          { title: 'Admin', value: 'admin' },
                          { title: 'Publisher', value: 'publisher' },
                          { title: 'User', value: 'user' }
                        ]"
                        item-text="title"
                        item-value="value"
                        :label="$t('forms.labels.selectPermissions')"
                        multiple
                        chips
                        outlined
                        dense
                      ></v-select>
                    </v-form>
                  </v-col>
                </v-row>
              </v-container>
            </v-card-text>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="error" @click="closeDialog">
                Close
              </v-btn>
              <v-btn color="success" @click="save">
                Save
              </v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
      </v-row>
      <v-row justify="center">
        <v-dialog v-model="removeDialog" persistent max-width="600px">
          <v-card>
            <v-card-text>
              <v-container>
                <v-row>
                  <v-col>
                    <h2>Are You Sure?</h2>
                  </v-col>
                </v-row>
              </v-container>
            </v-card-text>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="error" @click="removeDialog = false">
                Close
              </v-btn>
              <v-btn color="success" @click="save">
                Remove
              </v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
      </v-row>
    </v-container>
  </v-card-text>
</template>

<script>
import draggable from 'vuedraggable'
import validations from '@/mixins/validations'
export default {
  components: {
    draggable
  },
  mixins: [validations],
  props: {
    menu: {
      type: Object,
      required: true
    }
  },
  data() {
    return {
      drag: false,
      dialog: false,
      edit: false,
      remove: false,
      itemIndex: null,
      childrenIndex: null,
      editType: '',
      deleteType: '',
      removeDialog: '',
      menuItem: {
        title: '',
        icon: '',
        route: '',
        children: [],
        loggedIn: true,
        loggedOut: true,
        permission: [],
        translation: ''
      },
      defaultItem: {
        title: '',
        icon: '',
        route: '',
        children: [],
        loggedIn: true,
        loggedOut: true,
        permission: [],
        translation: ''
      }
    }
  },
  methods: {
    addItem() {
      const newItem = Object.assign({}, this.menuItem)
      newItem.children = []
      this.menu.items.push(newItem)
      this.$refs.menuItemForm.resetValidation()
      // this.$refs.menuItemForm.reset()
      this.menuItem = Object.assign({}, this.defaultItem)
      this.dialog = false
    },
    editItem() {
      if (this.editType === 'item') {
        this.menu.items[this.itemIndex] = Object.assign({}, this.menuItem)
      } else {
        this.menu.items[this.itemIndex].children[
          this.childrenIndex
        ] = Object.assign({}, this.menuItem)
      }
      this.$refs.menuItemForm.resetValidation()
      // this.$refs.menuItemForm.reset()
      this.menuItem = Object.assign({}, this.defaultItem)
      this.dialog = false
    },
    deleteItem() {
      if (this.deleteType === 'item') {
        this.menu.items.splice(this.itemIndex, 1)
      } else {
        this.menu.items[this.itemIndex].children.splice(this.childrenIndex, 1)
      }
      this.removeDialog = false
      this.remove = false
    },
    closeDialog() {
      this.$refs.menuItemForm.resetValidation()
      // this.$refs.menuItemForm.reset()
      this.menuItem = Object.assign({}, this.defaultItem)
      this.dialog = false
    },
    editDialog(item, index, i, editType) {
      this.edit = true
      this.editType = editType
      this.itemIndex = index
      this.childrenIndex = i
      this.menuItem = Object.assign({}, item)
      this.dialog = true
    },
    deleteDialog(item, index, i, deleteType) {
      this.remove = true
      this.deleteType = deleteType
      this.itemIndex = index
      this.childrenIndex = i
      this.removeDialog = true
    },
    addDialog() {
      this.dialog = true
      this.edit = false
      this.itemIndex = null
      this.childrenIndex = null
    },
    save() {
      if (this.remove) {
        this.deleteItem()
      } else {
        this.$refs.menuItemForm.validate()
        if (this.valid) {
          if (this.edit) {
            this.editItem()
          } else {
            this.addItem()
          }
        }
      }
    }
  }
}
</script>

<style lang="css">
